@use 'core/variables' as *;
@use 'core/mixins' as *;
@use '../core/extend' as *;
.page {
	width: 100vw;

	// .page__portfolio-section
	&__portfolio-section {
		position: relative;
	}

	// .page__content
	&__content {
		padding-top: 20px;
	}

	// .page__header
	&__header {
		position: sticky;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 11;
		height: 8.8rem;
		display: flex;
		align-items: center;
		background: #fff;
		transition: box-shadow 0.9s ease-in-out;

		&.with-shadow {
			box-shadow: 0 2px 9px #6c8fac;
			background: #fff;
		}

		@include screen-768 {
			padding: 5px 0;
		}

		.header__image {
			background: url('../img/icons/logo-b.png') center no-repeat;
		}

		.menu-list__link {
			&:hover {
				color: #000;
			}

			@extend .underline;
		}

		.header__title {
			transition: opacity 0.4s ease-in-out;

			&:hover {
				opacity: 0.6;
			}
		}
	}

