	// .page__header-project
	&__header-project {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 11;
		height: 8.8rem;
		display: flex;
		align-items: center;
		transition: all 0.3s ease-in-out;

		.header__body {
			color: #fff;
		}

		.burger-button__items span {
			background: #fff;
		}

		&.with-shadow {
			box-shadow: 0 2px 9px #6c8fac;
			background: #fff;
			color: #000;

			.icon-volume-phone {
				&::before {
					color: #000;
				}
			}

			.burger-button__items span {
				background: #000;
			}

			.header__body {
				color: #000;
			}

			.header__image {
				background: url('../img/icons/logo-b.png') center no-repeat;
			}

			.menu-list__sub-menu {
				color: #fff;
				ul {
					background: #fff;
				}
			}

			.menu-list__item {
				color: #000;
			}

			.menu-list__link {
				@extend .underline;
				&:hover {
					color: #000;
				}
			}
		}

		@include screen-768 {
			padding: 5px 0;
		}

		.header__image {
			background: url('../img/icons/logo-w.png') center no-repeat;
		}

		.menu-list__sub-menu {
			color: #fff;
			ul {
				background: $color;
			}
		}

		.menu-list__link {
			@extend .underline-w;

			&:hover {
				color: #fff;
			}
		}

		.header__title {
			transition: opacity 0.4s ease-in-out;

			&:hover {
				opacity: 0.6;
			}
		}
	}

	// page__performance
	&__performance {
		margin-top: -20px;
		padding: 0 0 13rem;

		@include screen-690 {
			padding: 0 0 50px;
		}
	}

	// .page__services
	&__services {
		position: relative;
		z-index: 9;
		display: flow-root;
		padding-block: 0 13rem;

		@include screen-690 {
			padding-block: 0 60px;
		}
	}

