	// .page__project
	&__project {
		position: relative;
		padding-block: 0 13rem;

		@include screen-1140 {
			padding-block: 0 50px;
		}
	}

	// .page__schedule
	&__schedule {
		overflow: hidden;
		padding-block: 0 13rem;

		@include screen-690 {
			padding-block: 0 50px;
		}
	}

	// .page__services-price
	&__services-price {
		// padding: 0 50px 16.2rem;
		padding-block: 0 13rem;
		scroll-margin-top: 100px;

		@include screen-768 {
			scroll-margin-top: 60px;
		}

		@include screen-690 {
			padding-block: 0 60px;
		}
	}

	// .page__about-company
	&__about-company {
		scroll-margin-top: 100px;
		padding-block: 0 13rem;

		@include screen-690 {
			padding-block: 0 60px;
		}
	}

	// .page__order-place
	&__order-place {
		padding-block: 0 13rem;

		@include screen-690 {
			padding-block: 0 60px;
		}
	}

	// .page__seo-block
	&__seo-block {
		background: url('../img/main/s-bg.png') center / cover repeat;
		padding-block: 12rem 13rem;

		@include screen-690 {
			padding-block: 50px 60px;
		}
	}

	// .page__company-team
	&__company-team {
		padding-block: 0 13rem;

		@include screen-690 {
			padding-block: 0 60px;
		}
	}

	// .page__answers
	&__answers {
		padding-block: 0 13rem;

		@include screen-690 {
			padding-block: 0 60px;
		}
	}

	// .page__order-portfolio
	&__order-portfolio {
		background: url('../img/main/fon.png') center / cover no-repeat;
	}

	// .page__page-unknown
	&__page-unknown {
		background: url('../img/portfolio/project-smt/fon.png') center / cover
			no-repeat;
	}

	// .page__design-concept
	&__design-concept {
		background: rgb(2, 2, 2);
	}

	// .page__request-audit
	&__request-audit {
		padding: 0 50px 16.2rem;

		@include screen-1440 {
			padding: 0 30px 16.2rem;
		}
		@include screen-690 {
			padding: 0 30px 60px;
		}

		@include screen-375 {
			padding: 0 10px 60px;
		}
	}

	// .page__answers-project
	&__answers-project {
		padding: 0 50px 16.2rem;

		@include screen-1440 {
			padding: 0 10px 16.2rem;
		}

		@include screen-1440 {
			padding: 0 10px 60px;
		}
	}

	// .page__brands
	&__brands {
		height: 100%;
		padding: 0 0 16.2rem;

		@include screen-690 {
			padding: 0 0 60px;
		}
	}

	// .page__info-block
	&__info-block {
		position: relative;
		padding: 0 50px 16.2rem;

		@include screen-1440 {
			padding: 0 30px 16.2rem;
		}

		@include screen-690 {
			padding: 0 30px 60px;
		}

		@include screen-375 {
			padding: 0 10px 60px;
		}
	}

	//page__dev-info
	&__dev-info {
		position: relative;
		padding: 0 0 13rem;

		@include screen-690 {
			padding: 0 0 50px;
		}
	}
}
